import React, { Component } from 'react';
import { Table, Divider, Spin } from 'antd';
import moment from 'moment';
import request from '../utilities/request';
import api from '../config/api';

const columns = [{
  title: 'ชื่อวิชา',
  dataIndex: 'subject',
  key: 'subject',
  render: text => <a href="javascript:;">{text}</a>,
}, {
  title: 'รายละเอียด',
  dataIndex: 'description',
  key: 'description',
}, {
  title: 'ประเภท',
  dataIndex: 'type',
  key: 'type',
}, {
  title: 'วันกำหนดส่ง',
  dataIndex: 'date',
  key: 'date',
}, {
  title: 'เวลากำหนดส่ง',
  dataIndex: 'time',
  key: 'time',
}, {
  title: 'แจ้งล่วงหน้า​ (วัน)',
  dataIndex: 'appointment',
  key: 'appointment',
}, {
  title: 'Action',
  key: 'action',
  render: (text, record) => (
    <span>
      <a href="javascript:;">แก้ไข</a>
      <Divider type="vertical" />
      <a href="javascript:;">ลบ</a>
    </span>
  ),
}];

class TableItems extends Component {
  state = {
    isLoading: true,
    data: []
  };

  componentDidMount() {
    this.getData();
  }

  removeHomework = () => {

  }

  updateHomework = () => {

  }

  getData = async () => {
    const { statusCode, data } = await request.get(api.url.homework)
    if (statusCode) {
      const convertedData = data.map((item, key) => {
        const {
          subject,
          description,
          type,
          due_date,
          due_time,
          appointment
        } = item;

        console.log(moment.locale());

        return {
          key,
          subject,
          description,
          type,
          date: moment(due_date).format('LL'),
          time: moment(due_time).format('LT'),
          appointment,
        };
      });
      this.setState({
        isLoading: false,
        data: convertedData
      });
    }
  }

  render() {
    const { isLoading, data } = this.state;

    if (isLoading)
      return (<Spin />)

    return (
      <Table columns={columns} dataSource={data} />
    );
  }
}

export default TableItems;


